@import url('https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap');
a{text-decoration: none;color:white;}
ul{list-style: none;}

.navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding:0 20%;
    color:white;
    width: 100%;
    height:40px;
    background-color: rgb(0, 0, 0);
    z-index: 1;
}

.middle{
    display: flex;
}

.middle div{
    margin:0 30px;
}

.logo{
    display: flex;
    align-items:center;
    font-size: 24px;
}

.logo .logo-btn{
    color:rgb(0, 255, 0);
    font-size: 35px;
}