.movieList{
    width: 90%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
    margin-bottom: 30px;
}

.listContent{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.loadItems{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    background:#ff9900;
    color:black;
    cursor:pointer;
    padding:0 10px;
    border-radius:10px;
    margin-bottom:20px;
    font-size: 15px;
}

.movieRow--item{
    width: 100%;
    height:100%;
}

.img{
    width: 100%;
    height:300px;
}

.movieRow--item img{
    width: 100%;
    height: 100%;
    border-radius:8px;
}
.movieCard{
    position: relative;
    width: 200px;
    text-decoration: none;
    color:white;
    margin:15px 30px;
    transition: 0.2s;
}

.movieCard:hover{
    transition: -webkit-transform .3s;
    transition: transform .3s;
    transition: transform .3s, -webkit-transform .3s;
    -webkit-transform: scale(1.1);  
    transform: scale(1.1);
    border-radius:5px;
    background:#ff9900;
    color:black;
}

.voteCard{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    position: absolute;
    top:0;
    font-size:18px;
    padding:0 5px;
    line-height: 17px;
    background:rgb(0, 0, 0);
    color:#ffffff;
    border-radius: 0 0 10px 0;
}

.star{
    color:yellow;
}

.movieCard:hover img{
    border-radius: 0
}

.innerInfo{
    height: 100%;
}

.info {
    position:absolute;
    top:0;
    width:100%;
    height:300px;
    /*background:rgba(0, 0, 0, 0.541);*/
    position:absolute;
    display: block;
    transition: 0.5s;
}

.title{
    font-size: 15px;
    margin:0 0 5px 5px;
}

.year p{
    font-size: 13px;
    display: inline-block;
    color:black;
    background:#ff9900;
    padding:0 10px;
    margin-bottom:5px;
}

.movieGenre{
    height: 40px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    color: white;
}

.linkButton{
    padding-top:10px;
    transition:0.2s;
}
.linkButton:hover{
    background:#ff9900;
}

.activeBtn{
    border-top:2px solid #ff9900;
}
.movieInfo{
    position:relative;
    overflow: hidden;
}
.movieInfoBackground{
    filter: blur(8px);
    -webkit-filter: blur(8px);
    opacity: 0.4;
    width: 100%;
    height: 100vh;
    overflow: hidden;
}

.poster{
    width: 400px;
    height:600px;
    border-radius:10px; 
    box-shadow: 0px 0px 22px 7px #000000;
}

.details{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    padding:0 40px;
}

.infoMovieTitle{
    font-size: 40px;
    margin-bottom:20px;
}


.contentInfo{
    width: 80%;
    position:absolute;
    top:50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    display: -webkit-flex;
    display: flex;
}

.synopsis{
    color:rgb(255, 153, 0);
    margin-bottom: 5px;
}

.overview{
    line-height: 25px;
}
.video-responsive {
    overflow: hidden;
    padding-bottom: 56.25%;
    position: relative;
    height: 0;
  }
  
  .video-responsive iframe {
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    position: absolute;
  }
a{text-decoration: none;color:white;}
ul{list-style: none;}

.navbar{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    padding:0 20%;
    color:white;
    width: 100%;
    height:40px;
    background-color: rgb(0, 0, 0);
    z-index: 1;
}

.middle{
    display: -webkit-flex;
    display: flex;
}

.middle div{
    margin:0 30px;
}

.logo{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items:center;
            align-items:center;
    font-size: 24px;
}

.logo .logo-btn{
    color:rgb(0, 255, 0);
    font-size: 35px;
}
* {box-sizing:border-box}
.slideshow{
    position: relative;
    overflow: hidden;
    margin:0;
    padding:0;
}

.each-slide{
    width: 100%;
    height: 100vh;
}

.each-slide img{
    width: 100%;
    height:100%;
    object-fit: cover;
    object-position: 0% 0%;
}

.indicators{
    width: 100%;
    position:absolute;
    bottom:20px;
}

.indicator{
    max-width: 140px;
    max-height:190px;
    cursor:pointer;
    margin:0 10px;
}

.indicator img{
    max-width: 100%;
    max-height:100%;
    border-radius:10px;
    transition:.3s;
    opacity: 0.5;
}

.voteAverage{
    display: inline-block;
    background:yellow;
    color:black;
    padding: 0 10px;
}
.indicator img:hover{
    transition: -webkit-transform .3s;
    transition: transform .3s;
    transition: transform .3s, -webkit-transform .3s;
    -webkit-transform: scale(1.2);  
    transform: scale(1.2);
    opacity: 1;
}

.indicator.active img{
    transition: -webkit-transform .3s;
    transition: transform .3s;
    transition: transform .3s, -webkit-transform .3s;
    -webkit-transform: scale(1.2);  
    transform: scale(1.2); 
    box-shadow: 0px 0px 10px 2px #000000;
    border:1px solid white;
    opacity: 1;
}

.slideInfo{
    position:absolute;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    z-index: 999;
    width: 40vw;
    margin:0 0 0 150px;
    height:100%;
}

.slideInfo div h1{
    font-size: 40px;
}

.slideInfo .playButton{
    color:black;
    display: -webkit-inline-flex;
    display: inline-flex;
    background: white;
    padding: 10px 15px;
    border-radius: 5px;
    cursor: pointer;
}

.playButton{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}
.play{
    color:black;
    margin-right:8px;
}
.backdrop_path{
    opacity: 0.5;
}

.filter{
    width: 100%;
    height: 100%;
    position: absolute; 
    box-shadow: inset 0px 0px 50px 30px rgba(0,0,0,0.49);
}

.react-slideshow-container .nav{
    height: 100vh;
    transition: 0.2s;
}

.react-slideshow-container .nav:hover{
    background: rgba(0, 0, 0, 0.534);
}

